import React, { Component } from 'react';

class Total extends Component {
    render() {
        var { cart } = this.props;

        return (
            <tr>
                <td colSpan="3"></td>
                <td>
                    <h4>
                        <strong>TOTAL</strong>
                    </h4>
                </td>
                <td>
                    <h4>
                        <strong>{this.showTottalAmount(cart)}</strong>
                    </h4>
                </td>
                <td colSpan="3">
                    <button type="button" className="btn btn-primary waves-effect waves-light">Complete purchase
                <i className="fa fa-angle-right right"></i>
                    </button>
                </td>
            </tr>
        )
    }
    showTottalAmount = (cart)=>{
        var total = 0;
        if(cart.length >0){
            for(var i = 0; i < cart.length; i++){
                total += cart[i].product.price*cart[i].quality
            }
        }
        return total;
    }
}

export default Total;
